import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ExpedientesPage } from '../expedientes/expedientes';
import { NotificacionPage } from '../notificacion/notificacion';
import { AjustesPage } from '../ajustes/ajustes';
import { Storage } from '@ionic/storage';
import {LoginPage} from '../login/login';

@Component({
  templateUrl: 'tabs.html'
})
export class TabsPage {

  tab1Root = ExpedientesPage;
  tab2Root = NotificacionPage;
  tab3Root = AjustesPage;
  
  constructor(public navCtrl: NavController, private storage: Storage)  {
 	this.storage= storage;
 	this.storage.get('user').then((user) => {
 		console.log(user);
 		if (user == undefined) {this.navCtrl.push(LoginPage);
 		}})
            

  }
  
}


