import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import {LoginPage} from '../login/login';
import {App} from 'ionic-angular';

/**
 * Generated class for the AjustesPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-ajustes',
  templateUrl: 'ajustes.html',
})
export class AjustesPage {
    public event = [{
    timeStarts: '00:00',

  },
  {
    timeStarts: '00:00',

  },
  {
    timeStarts: '00:00',

  },
  {
    timeStarts: '00:00',

  },
  {
    timeStarts: '00:00',

  },
  {
    timeStarts: '00:00',

  },
  {
    timeStarts: '00:00',

  },
  {
    timeStarts: '00:00',

  },
  {
    timeStarts: '00:00',

  },
  {
    timeStarts: '00:00',

  },
  {
    timeStarts: '00:00',

  },
  {
    timeStarts: '00:00',

  },
  {
    timeStarts: '00:00',

  },
  {
    timeStarts: '00:00',

  },
    ]
    items = [
    'Cerrar Sesión'
  ];

  

  constructor(public navCtrl: NavController, public navParams: NavParams, public alerCtrl: AlertController, private storage: Storage, private app: App) {
    this.storage=storage;
    this.storage.get('event').then((event) => {
    console.log(event);
    if (event != undefined) {
      this.event=JSON.parse(event);
    }});
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AjustesPage');
  }
  Guardar(){
    console.log(JSON.stringify(this.event));
    this.storage.set('event',JSON.stringify(this.event));
     let alert = this.alerCtrl.create({
            title: 'Exito',
            subTitle: 'Se ha guardado correctamente',
            buttons: ['OK']
        });
        alert.present();
  }
   doConfirm() {
    let confirm = this.alerCtrl.create({
      title: 'Cerrar Sesión',
      message: '¿Desea cerrar sesión?',
      buttons: [
        {
          text: 'No',
          handler: () => {
            console.log('Disagree clicked');
          }
        },
        {
          text: 'Si',
          handler: () => {
            this.storage.clear();
            //this.navCtrl.push(LoginPage);
            this.app.
            getRootNav().setRoot(LoginPage);
            console.log('Agree clicked');
          }
        }
      ]
    });
    confirm.present()
  }

}



