@NgModule({
  declarations: [
    MyApp,
    AjustesPage,
    ExpedientesPage,
    LoginPage,
    NotificacionPage,
    HomePage,
    MapaPage,
    AsuntoPage,
    TabsPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/mapa/mapa.module#MapaPageModule', name: 'MapaPage', segment: 'mapa', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/expedientes/expedientes.module#ExpedientesPageModule', name: 'ExpedientesPage', segment: 'expedientes', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/asunto/asunto.module#AsuntoPageModule', name: 'AsuntoPage', segment: 'asunto', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/notificacion/notificacion.module#NotificacionPageModule', name: 'NotificacionPage', segment: 'notificacion', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ajustes/ajustes.module#AjustesPageModule', name: 'AjustesPage', segment: 'ajustes', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot(),
   // CloudModule.forRoot(cloudSettings),
  // FCM,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyC0bz6TJFaXxjFEvRRg73f5I_CrrGiLV6A'
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AjustesPage,
    ExpedientesPage,
    LoginPage,
    NotificacionPage,
    HomePage,
    MapaPage,
    AsuntoPage,
    TabsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    FCM,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



