import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform, ActionSheetController} from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { LoadingController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AlertController } from 'ionic-angular';

/**
 * Generated class for the MapaPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-mapa',
  templateUrl: 'mapa.html',
})
export class MapaPage {
	latitud: number=-33.564715;
  longitud: number=-70.5516629;
   help: string = "info";
  isAndroid: boolean = false;


  expediente: any = {mensaje:'holaa'};
 

  constructor(public navCtrl: NavController, public navParams: NavParams, public platform: Platform, public actionsheetCtrl: ActionSheetController,public http: Http, public loadingCtrl: LoadingController, public alertCtrl: AlertController, private storage: Storage) {

  	 this.isAndroid = platform.is('android');
     this.storage=storage;
     var expediente=(navParams.get('expediente'));
     this.expediente = expediente;
     this.latitud=expediente.latitud;
     this.longitud=expediente.longitud;
  }

EnviarMensaje(mensaje){
  
    this.storage.get('user').then((user) => {
    console.log(user);
    if (user != undefined) {
        let loader = this.loadingCtrl.create({
      content: "Espere por favor...",
      //duration: 
    });

    loader.present();
    

 this.storage.get('user').then((user) => {
    var link = 'http://cbms.sistemazeus.cl/api/viper/info/'+user+'?mensaje='+mensaje+'&expediente='+this.expediente.id;
   // var link = 'http://192.168.1.24:3000/api/viper/info/'+user+'?mensaje='+mensaje+'&expediente='+this.expediente.id;
      var myData = JSON.stringify({user:user,mensaje:mensaje});

     this.http.put(link,myData)
  .subscribe(data => {
    loader.dismissAll();
    console.log(data);
  let alert = this.alertCtrl.create({
    title: 'ZeusAlert',
    subTitle: 'Mensaje recibido',
    buttons: ['OK']
  });
  alert.present();

  
  }, error => {


    loader.dismissAll();
       let alert = this.alertCtrl.create({
            title: 'Error',
            subTitle: 'Falló la conexión con el servidor',
            buttons: ['OK']
        });
        alert.present();
    console.log("Oooops!");
  });

  })

}

  })

}


openMenu() {
    let actionSheet = this.actionsheetCtrl.create({
      title: 'Confirmación',
      cssClass: 'action-sheets-basic-page',
      buttons: [
        {
          text: 'Me dirijo',
          icon: !this.platform.is('ios') ? 'car' : null,
          handler: () => {
            this.EnviarMensaje('Me dirijo')
            console.log('gone clicked');
          }
        },
        {
          text: 'Voy al cuartel',
          icon: !this.platform.is('ios') ? 'home' : null,
          handler: () => {
            console.log('station clicked');
             this.EnviarMensaje('Voy al cuartel')
          }
        },
        {
          text: 'No voy',
          icon: !this.platform.is('ios') ? 'close' : null,
          handler: () => {
            console.log('Favorite clicked');
             this.EnviarMensaje('No voy')
          }
        },
        {
          text: 'Cancelar',
          role: 'cancel', // will always sort to be on the bottom
          //icon: !this.platform.is('ios') ? 'close' : null,
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

  openWaze(){
    if (this.platform.is('ios')) {
          window.open('maps://?q=' + this.expediente.acto + '&saddr=' + this.latitud + ',' + this.longitud + '&daddr=' + this.latitud+ ',' + this.longitud, '_system');
        };
        // android
        if (this.platform.is('android')) {
          window.open('geo://' + this.latitud + ',' + this.longitud + '?q=' + this.latitud + ',' + this.longitud + '(' + this.expediente.acto + ')', '_system');
        };
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MapaPage');

  }

}




