import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the AsuntoPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-asunto',
  templateUrl: 'asunto.html',
})
export class AsuntoPage {
	notificacion: any = {mensaje:'holaa'};

  constructor(public navCtrl: NavController, public navParams: NavParams) {

  	 var notificacion=(navParams.get('notificacion'));
  	 console.log(notificacion);
  	 this.notificacion=notificacion;
  }

mensaje(){

	return this.notificacion.mensaje;
}
  ionViewDidLoad() {
    console.log('ionViewDidLoad AsuntoPage');
  }

}



