import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AsuntoPage } from '../asunto/asunto';
import { AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { Http } from '@angular/http';
import { LoadingController } from 'ionic-angular';

/**
 * Generated class for the NotificacionPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-notificacion',
  templateUrl: 'notificacion.html',
})
export class NotificacionPage {
	notificacion: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage, public http: Http, public alertCtrl: AlertController,public loadingCtrl: LoadingController) {
  	this.storage=storage;
  	this.storage.get('user').then((user) => {
 		console.log(user);
 		if (user != undefined) {
 			  let loader = this.loadingCtrl.create({
      content: "Espere por favor...",
      //duration: 
    });

    loader.present();
    
    var link = 'http://cbms.sistemazeus.cl/api/viper/info/'+ user;

    
     this.http.get(link)
 	.subscribe(data => {
 		loader.dismissAll();
 		console.log(data);
 		this.notificacion=JSON.parse(data['_body']);
 	}, error => {


	 	loader.dismissAll();
 			 let alert = this.alertCtrl.create({
      			title: 'Error',
      			subTitle: 'Falló la conexión con el servidor',
      			buttons: ['OK']
    		});
    		alert.present();
 		console.log("Oooops!");
 	});
}

  })
  }
  doRefresh(refresher){
    this.storage.get('user').then((user) => {
    console.log(user);
    if (user != undefined) {
        let loader = this.loadingCtrl.create({
      content: "Espere por favor...",
      //duration: 
    });

    loader.present();
    
   var link = 'http://cbms.sistemazeus.cl/api/viper/info/'+ user;
       // var link = 'http://192.168.1.24:3000/api/viper/info/'+ user;
     this.http.get(link)
  .subscribe(data => {
    loader.dismissAll();
    refresher.complete();
    console.log(data);
    this.notificacion=JSON.parse(data['_body']);
  }, error => {


    loader.dismissAll();
       let alert = this.alertCtrl.create({
            title: 'Error',
            subTitle: 'Falló la conexión con el servidor',
            buttons: ['OK']
        });
        alert.present();
        refresher.complete();
    console.log("Oooops!");
  });
}

  })
  }
  GoAsunto(notificacion){
  	this.navCtrl.push(AsuntoPage,{notificacion:notificacion});

  }

  

  ionViewDidLoad() {
    console.log('ionViewDidLoad NotificacionPage');
  }

}



