import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { MapaPage } from '../mapa/mapa';
import { Http } from '@angular/http';
import { LoadingController } from 'ionic-angular';

/**
 * Generated class for the ExpedientesPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-expedientes',
  templateUrl: 'expedientes.html',
})
export class ExpedientesPage {
	

	mapa = MapaPage;
	expedientes: any;


  constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage, public http: Http, public alertCtrl: AlertController,public loadingCtrl: LoadingController) {
  	this.storage=storage;
  	this.ionViewWillEnter();
  }

  doRefresh(refresher){
    this.storage.get('user').then((user) => {
    console.log(user);
    if (user != undefined) {
        let loader = this.loadingCtrl.create({
      content: "Espere por favor...",
      //duration: 
    });

    loader.present();
    
    var link = 'http://cbms.sistemazeus.cl/api/viper/info?user='+ user;
     this.http.get(link)
  .subscribe(data => {
    loader.dismissAll();
    refresher.complete();
    console.log(data);
    this.expedientes=JSON.parse(data['_body']);
  }, error => {


    loader.dismissAll();
       let alert = this.alertCtrl.create({
            title: 'Error',
            subTitle: 'Falló la conexión con el servidor',
            buttons: ['OK']
        });
        alert.present();
        refresher.complete();
    console.log("Oooops!");
  });
}

  })

  };

  fecha(x){
    var f = new Date(x);
    var hora = (f.getHours() > 10 ?'' : '0')+f.getHours();

    var minutos = (f.getMinutes() > 10 ?'' :'0')+f.getMinutes();
    
    return f.getDate()+'-'+(f.getMonth()+1)+'-'+f.getFullYear() +' '+hora+':'+minutos;
  }
  GoTo(expediente){
  	this.navCtrl.push(MapaPage,{expediente:expediente});

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ExpedientesPage');
  }

 ionViewWillEnter() { 
  this.storage.get('user').then((user) => {
    console.log(user);
    if (user != undefined) {
        let loader = this.loadingCtrl.create({
      content: "Espere por favor...",
      //duration: 
    });

    loader.present();
    
   var link = 'http://cbms.sistemazeus.cl/api/viper/info?user='+ user;
   //   var link = 'http://192.168.1.24:3000/api/viper/info?user='+ user;
     this.http.get(link)
  .subscribe(data => {
    loader.dismissAll();
    console.log(data);
    this.expedientes=JSON.parse(data['_body']);
  }, error => {


    loader.dismissAll();
       let alert = this.alertCtrl.create({
            title: 'Error',
            subTitle: 'Falló la conexión con el servidor',
            buttons: ['OK']
        });
        alert.present();
    console.log("Oooops!");
  });
}

  })
  }
}



