import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ExpedientesPage} from '../expedientes/expedientes';
import { LoadingController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { TabsPage } from '../tabs/tabs';
import { FCM } from '@ionic-native/fcm';

/**
 * Generated class for the LoginPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
	expedientes = ExpedientesPage;

  data: Object = {};
  constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http, public loadingCtrl: LoadingController, public alertCtrl: AlertController, private storage: Storage, public fcm: FCM) {

	this.data = {} 
	this.storage= storage;
	
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  presentLoading() {
    let loader = this.loadingCtrl.create({
      content: "Espere por favor...",
      //duration: 
    });


var data =this.data;
    this.fcm.getToken().then(token=>{
    loader.present();
    console.log(loader);
    console.log(data);
    data['token'] = token;
    var link = 'http://cbms.sistemazeus.cl/api/viper/info';
	  //var link = 'http://192.168.1.24:3000/api/viper/info';
  

  var myData = JSON.stringify(data);
     this.http.post(link, data)
 	.subscribe(data => {
 		loader.dismissAll();
 		var usuario = JSON.parse(data['_body']);
 		if(usuario.login!=true){
 			 let alert = this.alertCtrl.create({
      			title: 'Alerta',
      			subTitle: 'Correo o contraseña incorrecta',
      			buttons: ['OK']
    		});
    		alert.present();
 		} else {
 			this.storage.set('user', usuario.user);
 			this.storage.set('nombre', usuario.nombre);
 			this.storage.set('apellido', usuario.apellido);
 			 this.navCtrl.push(TabsPage);


 		}
 		console.log(data);
     	//this.data.response = data[""]; 
	 }, error => {


	 	loader.dismissAll();
 			 let alert = this.alertCtrl.create({
      			title: 'Alerta',
      			subTitle: 'No es posible la conexión con el servidor',
      			buttons: ['OK']
    		});
    		alert.present();
 		console.log("Oooops!");
 	});

  });

  }

}



